import { Component } from 'react';

const token = '9a0c090405d89a14fd4dc83c35bfff0bdcd39272';

export default class Issues extends Component {
  state = {
    issues: []
  };

  getIssues() {
    fetch('https://api.github.com/search/issues?q=davidgumzchoi', {
      headers: {
        Authorization: `token ${token}`
      }
    })
      .then(response => response.json())
      .then(data => {
        console.log(data);
        this.setState({ issues: data });
      })
      .catch(error => console.error(error));
  }

  componentDidMount() {
    this.getIssues();
  }

  render() {
    return (
      <ul>
        {this.state.issues &&
          this.state.issues.map(issue => {
            <li>{issue.title}</li>;
          })}
      </ul>
    );
  }
}
